* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.switcher {
  height: 50vh;
  color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
}

.switcher__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #1e90ff;
  border: none;
  padding: 15px 32px;
  text-decoration: none;
}

.switcher__button:hover {
  background-color: #4682b4;
}

.switcher__button:first-child {
  margin-right: 20px;
}

.switcher__button:disabled {
  color: gray;
  background-color: #b0c4de;
}

.timer {
  width: 225px;
  justify-content: space-between;
  margin-top: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.field .value {
  font-size: 34px;
}

.field .label {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
}

form {
  width: 680px;
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

form input {
  display: block;
}

form button {
  align-self: flex-end;
}

